<template lang="html">
  <div id="agentAdd">
    <h1>新增代理商</h1>

    <div class="table add">
      <tbody>
      <tr>
        <th>代理手机</th>
        <td><input type="text" name="mobile" placeholder="代理手机号，用于登录" v-model="$$data.mobile"></td>
      </tr>
      <tr>
        <th>代理名称</th>
        <td><input type="text" name="name" placeholder="输入商户名称" v-model="$$data.name"></td>
      </tr>
      <tr>
        <th>所在地</th>
        <td><input type="text" name="belongArea" placeholder="省/市/街道" v-model="$$data.belongArea"></td>
      </tr>
      <tr>
        <th>结算卡</th>
        <td><input type="text" name="bankCard" placeholder="输入一级代理银行卡号" v-model="$$data.bankCard"></td>
      </tr>
      <tr>
        <th>开户名</th>
        <td><input type="text" name="bankAccountName" placeholder="银行开户名称" v-model="$$data.bankAccountName"></td>
      </tr>
      <tr>
        <th>手机号</th>
        <td><input type="text" name="bankReserveMobile" placeholder="银行开户预留手机号" v-model="$$data.bankReserveMobile"></td>
      </tr>
      </tbody>
    </div>
    <div class="product">
      <p>配置产品：</p>
      <label v-for="(product,index) in $$products">
        <input class="check" type="radio" name="name" :value="index" v-model="id">
        <div class="product1">
          <table class="table table-bordered">
            <caption>{{product.productName}}</caption>
            <thead>
            <tr>
              <td>通道名称</td>
              <td>支付结算手续费</td>
              <td>结算时间</td>
              <td>提现结算费</td>
              <td>商户支付手续费</td>
              <td>商户提现手续费</td>
            </tr>
            </thead>
            <tbody>
            <tr v-for="channel in product.list">
              <td>{{channel.channelTypeSign}}</td>
              <td><input type="text" name="name" v-model="channel.paymentSettleRate">%</td>
              <td>{{channel.productBalanceType}}</td>
              <td><input type="text" name="name" v-model="channel.withdrawSettleFee">元/笔</td>
              <td><input type="text" name="name" v-model="channel.merchantSettleRate">%</td>
              <td><input type="text" name="name" v-model="channel.merchantWithdrawFee">元/笔</td>
            </tr>
            </tbody>
          </table>
        </div>
      </label>
    </div>
    <div class="btn btn-default" @click="create">
      创建代理商
    </div>
  </div>
</template>

<script lang="babel">
  export default {
    name: 'dale',
    data () {
      return {
        mobile: '',
        name: '',
        belongArea: '',
        bankCard: '',
        bankAccountName: '',
        bankReserveMobile: '',
        product: {
          channels:[]
        },
        products:[],//所有产品
        id:''
      }
    },
    created: function () {
      this.$http.post('/product/list')
        .then(function (res) {
          this.$data.products=res.data;
          for (let i=0;i<this.$data.products.length;i++){
            for(let j=0;j<this.$data.products[i].list.length;j++){
              this.$data.products[i].list[j].paymentSettleRate='';
              (this.$data.products[i].list)[j].withdrawSettleFee='';
              (this.$data.products[i].list)[j].merchantSettleRate='';
              (this.$data.products[i].list)[j].merchantWithdrawFee='';
            }
          }
        },function (err) {
          this.$store.commit('MESSAGE_ACCORD_SHOW', {
            text: err.statusMessage
          })
        })
    },
    methods: {
      create: function () {
        this.$data.product.channels=[];
        this.$data.product.productId=this.$data.products[this.$data.id].productId
        for(let i=0;i<this.$data.products[this.$data.id].list.length;i++){
          this.$data.product.channels.push({
            channelType:this.$data.products[this.$data.id].list[i].channelTypeSign,
            paymentSettleRate:this.$data.products[this.$data.id].list[i].paymentSettleRate,
            settleType:this.$data.products[this.$data.id].list[i].productBalanceType,
            withdrawSettleFee:this.$data.products[this.$data.id].list[i].withdrawSettleFee,
            merchantSettleRate:this.$data.products[this.$data.id].list[i].merchantSettleRate,
            merchantWithdrawFee:this.$data.products[this.$data.id].list[i].merchantWithdrawFee
          })
        }
        let query = {
          mobile:this.$data.mobile,
          name:this.$data.name,
          belongArea:this.$data.belongArea,
          bankCard:this.$data.bankCard,
          bankAccountName:this.$data.bankAccountName,
          bankReserveMobile:this.$data.bankReserveMobile,
          product: this.$data.product
        }
        this.$http.post('/admin/addFirstDealer', query)
          .then(function (res) {
            this.$store.commit('MESSAGE_ACCORD_SHOW', {
              text: "添加成功"
            })
          }, function (err) {
            this.$store.commit('MESSAGE_ACCORD_SHOW', {
              text: err.statusMessage
            })
          })
      }
    },
    computed: {
      $$products: function() {
        return this.$data.products
      },
      $$data: function(){
        return this.$data;
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  h1, h2 {
    font-weight: normal;
    color: #337ab7;
    font-weight: bold;
    border-bottom: 2px solid #ccc;
    padding-bottom: 10px;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }

  .agentAdd {
    float: right;
    width: 80%;
  }

  .add {
    border: 1px solid #ccc;
    width: 800px;
    border-top: none;
    font-size: 16px;

    th {
      width: 200px;
      vertical-align: middle;
      text-align: center;
    }
    td input {
      border: none;
      width: 600px;
    }
  }

  .product1 {
    display: inline-block;
    width: 90%;
  }

  input.check {
    position: relative;
    top: -50px;
  }

  .product .table {
    display: inline-block;
    font-size: 12px;
    width: 90%;
    thead tr, caption {
      font-size: 8px;
      color: #777;
      background: #ccc
    }
    td {
      width: 16.5%;

      input {
        width: 77%;
        border: none;
      }
    }
  }
</style>



