<template lang="html">
  <div id="storeList">
    <h1>商户列表</h1>

    <div class="search">
      <label for="fun">支付方式：</label>
      <input type="text" name="fun" value="">
      <label for="name">通道名称：</label>
      <input type="text" name="name" value="">

      <div class="btn btn-primary">筛选</div>
    </div>
    <div class="table table-bordered">
      <thead>
      <tr>
        <th>商户编号</th>
        <th>商户名称</th>
        <th>所属代理商</th>
        <th>所属代理商编号</th>
        <th>注册时间</th>
        <th>可用状态</th>
        <th>操作</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="store in stores">
        <td>{{store.id}}</td>
        <td>{{store.merchantName}}</td>
        <td>合众易宝</td>
        <td>20001230000</td>
        <td>{{store.createTime}}</td>
        <td>{{store.status}}</td>
        <td>
          <div class="btn btn-primary" @click="audit($event,store.id)">审核</div>
        </td>
      </tr>
      </tbody>
    </div>
  </div>
</template>

<script lang="babel">
  export default {
    name: 'storeList',
    data () {
      return {
        stores: []
      }
    },
    created: function () {
      this.$http.post('/admin/query/getAll').then(function (res) {
        this.$data.stores = res.data;
      }, function (err) {
        this.$store.commit('MESSAGE_ACCORD_SHOW', {
          text: err.statusMessage
        })
      })
    },
    methods: {
      audit: function (event, id) {
        this.$router.push({
          path: '/record/StoreAudit', query: {
            id: id
          }
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  h1, h2 {
    font-weight: normal;
    color: #337ab7;
    font-weight: bold;
    border-bottom: 2px solid #ccc;
    padding-bottom: 10px;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }

  .storeList {
    float: right;
    width: 80%;
  }

  .search {
    margin-bottom: 10px;
    input {
      margin-right: 20px;
    }
  }

  .table {
    overflow: hidden;
    width: 1000px;
    td, th {
      height: 36px;
      line-height: 36px;
      text-align: center;
      width: 120px;
    }
    th {
      height: 28px;
      line-height: 28px;
    }
    td:nth-child(6), th:nth-child(6), td:nth-child(7), th:nth-child(7) {
      width: 110px;
    }
    td:nth-child(5), th:nth-child(5), td:nth-child(2), th:nth-child(2), td:nth-child(3), th:nth-child(3), {
      width: 180px;
    }
    tr:nth-child(2) {
      background: #ccc;
    }
  }
</style>



